import React, { Fragment, Component } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import posed, { PoseGroup } from "react-pose";
import { connect } from "react-redux";
import { convLang } from "../actions/rootAct";

//components import
import { Navbar, Footer } from "./layout";
import Home from "./Home";
import Experiences from "./Experiences";
import Services from "./Services";
import Portfolios from "./Portfolios";
import NoPage from "./404.js";
import Login from "./Login";
import Dashboard, {State} from "./admin"

const PoseEl = posed.div({
  enter: {
    opacity: 1,
    beforeChildren: true
  },
  exit: {
    opacity: 0
  }
});

class App extends Component {
  render() {
    const { rootData } = this.props;
    return (
      <Router>
        <Route
          render={({ location }) => (
            <Fragment>
              {/^\/(|experiences|services|portfolios)$/.test(location.pathname) && <Navbar data={rootData} convertLang={this.props.convertLang} />}
              <div id="box">
                <PoseGroup>
                  <PoseEl key={location.pathname}>
                    <Switch location={location}>
                      <Route
                        exact
                        path={"/"}
                        render={props => <Home data={rootData} {...props} />}
                        key="home"
                      />
                      <Route
                        path={"/experiences"}
                        render={props => (
                          <Experiences data={rootData} {...props} />
                        )}
                        key="exp"
                      />
                      <Route
                        path={"/services"}
                        render={props => (
                          <Services data={rootData} {...props} />
                        )}
                        key="services"
                      />
                      <Route
                        path={"/portfolios"}
                        render={props => (
                          <Portfolios data={rootData} {...props} />
                        )}
                        key="portfolios"
                      />
                      <Route path={"/login"} component={Login} />
                      <Route path={'/admin/dashboard'} component={Dashboard} />
                      <Route path={'/admin/state'} component={State} />

                      <Route component={NoPage} />
                    </Switch>
                  </PoseEl>
                </PoseGroup>
              </div>
              <Footer data={rootData} />
            </Fragment>
          )}
        />
      </Router>
    );
  }
}

const mapState = state => {
  return { rootData: state };
};

const mapDispatch = dispatch => {
  return {
    convertLang: lang => dispatch(convLang(lang))
  };
};

export default connect(
  mapState,
  mapDispatch
)(App);
