const rootRed = (state, action) => {
  if (action.type === "convLang") {
    switch (action.lang) {
      case "english":
        state = { ...state };
        state.lang = action.dataLang;
        break;
      case "indo":
        state = { ...state };
        state.lang = action.dataLang;
        break;
      case "sasak":
        state = { ...state };
        state.lang = action.dataLang;
        break;
      case "arab":
        state = { ...state };
        state.lang = action.dataLang;
        break;
      default:
        break;
    }
  }
  return state;
};

export default rootRed;
